body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  color: var(--black);
}

.lightMode {
  color: var(--white);
}

a {
  text-decoration: unset;
  color: unset;
}

span {
  font-family: inherit;
  font-style: inherit;
  font-weight: inherit;
  line-height: inherit;
  color: inherit;
}

input {
  font-family: 'Commissioner Regular';
  font-style: normal;
}

input::placeholder {
  font-family: 'Commissioner Regular';
  font-style: normal;
  color: var(--gray);
}

textarea {
  font-family: 'Commissioner Regular';
  font-style: normal;
}

textarea::placeholder {
  font-family: 'Commissioner Regular';
  font-style: normal;
  color: var(--gray);
}

@font-face {
  font-family: 'Commissioner Medium';
  src: url(assets/fonts/Commissioner/static/Commissioner-Medium.ttf) format('truetype');
}

@font-face {
  font-family: 'Commissioner Regular';
  src: url(assets/fonts/Commissioner/static/Commissioner-Regular.ttf) format('truetype');
}

:root {
  --black: #130914;
  --blue: #414feb;
  --white: #ffffff;
  --light-gray: rgba(140, 145, 181, 0.5);
  --gray: #8c91b5;
  --error: #f13f44;
  --success: #28bca1;
  --background: #f1f5f9;
}
